import React, { useState, useEffect, useRef } from "react";
import "../css/Setting.css";

import { ProgressBar } from "react-bootstrap";
import { useNavigate } from "react-router-dom";

import moment from "moment";
import { DatePicker, LocalizationProvider } from '@mui/x-date-pickers';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';






const Setting = () =>{
    const navigate = useNavigate();
    const [goalList, setGoalList]=useState([]);

    const [goal, setGoal] = useState("");

    // const editable = useRef(false);
    const [isEdit, setEdit] = useState(false);

    const today = new Date();
    const time = {
        year: today.getFullYear(),  // 
        month: today.getMonth() + 1, //  
        date: today.getDate(), //  
        hours: today.getHours(), // 
        minutes: today.getMinutes(), // 
      };

      
    const timestring = `${time.year}-${time.month}-${time.date}`;
    
    const [todaytime, setTodaytime] = useState(timestring);


    useEffect(()=>{
        console.log("todaytime: ", todaytime)
    },[])

    
    useEffect(()=>{
        console.log("goalList: ", goalList)
    },[goalList])

    // function toMain(){
    //     navigate("/main/"+ "");
    // }
    function toMain(){
        navigate("/main");
    }
    function toHome(){
        navigate("/");
    }

    const onChangeGoal=(e)=>{
        e.preventDefault();
        
        setGoal(e.target.value);
    }

      
    function editMode(){
        console.log("editmode");
        setEdit(!isEdit);

    }
    function handleOnKeyPress(e){
        if(e.key === "Enter"){
            addGoal();
        }

    }

    function addGoal(){
        const newGoal={
            id: 1,
            goal: goal
        }
        setGoalList(goalList.concat(newGoal));
        setGoal("");
    }
    function handleCheckGoal(){
        console.log("goalchecked");
    }
    function deleteGoals(){
        console.log("delete goals");
        setEdit(!isEdit);
    }

    return(
        <>
            <div className="setting_Top">
                <div className="setting_top_bar">
                    <ProgressBar style={{height:"30px", backgroundColor:"white"}} animated={true} now={60} variant="barColor"/>
                </div>
                <div className="setting_top_text">
                    <div>Reach Goal</div>
                    <div>increase the level of own life</div>
                </div>
            </div>
            <div className="setting_content_container">
                <div className="setting_input">
                    {isEdit?
                    <button className="setting_editmode" onClick={deleteGoals}>delete</button>
                    :
                    <button className="setting_standard" onClick={editMode}>edit</button>
                    }
                   

                    {/* <div className="setting_content_1"> */}
                    <input type= "text" className="setting_content_1" onChange={onChangeGoal}  onKeyPress={handleOnKeyPress} value={goal} placeholder="your goal"/>
                    {/* </div> */}


                    <div className="setting_content_2">
                        <LocalizationProvider dateAdapter={AdapterDateFns}>
                            <DatePicker 
                                label={'optional'}
                                views={['year', 'month', 'day']}
                                onChange={(newDate) => {console.log(newDate); setTodaytime(moment(newDate).format("DD-MM-YYYY"))}}
                                dateFormat="dd/MM/YYYY" 

                            />
                        </LocalizationProvider>
                    </div>
                </div>
                <div className="setting_goal_container">
                    {goalList.map((goal) =>(
                        <>
                        {isEdit ?
                            <>  
                                <div className="setting_editmode_goals">
                                    <input type="checkbox" checked={false} onChange={handleCheckGoal}/>
                                    <div> {goal.goal}</div>
                                </div>
                            </>
                        :
                        
                            <div>{goal.goal}</div>
                        }
                        </>
                    ))}
                </div>


            </div>

            <div className="setting_main_btn" onClick={toMain}>
                    MAIN()
            </div>
            
            <div className="setting_home_btn" onClick={toHome}>
                    HOME()
            </div>
        </>
    );
};

export default Setting;